.Loader {
    position: fixed;
    left: 0;
    top: 0;
    background: #eeeded;
    opacity: 0.8;
    z-index: 100;
    height: 100%;
    width: 100%;
  }
  
  .spinner {
    position: absolute;
    top: 30%;
    left: 40%;
    color: black;
    opacity: .8;
    z-index: 1000;
    font-family: "Fantasy";
  }